import * as React from "react";

import Chart from "./Chart";
import { Filter, Selection, countByFilter, Result, Nodes } from "../lib/helper";
import { data } from "../lib/data";

// rain: Filter;
//  sprinkler: Filter;
//  grass: Filter;

interface ContainerState {
  selection: Selection;
  values: Result;
}

const noSelection = {
  rain: Filter.none,
  sprinkler: Filter.none,
  grass: Filter.none
};

const noSelectionValue = countByFilter(data, noSelection);

export default class Container extends React.Component<undefined, ContainerState> {
  constructor(props: undefined) {
    super(props);
    this.updateFilter = this.updateFilter.bind(this);
    this.reset = this.reset.bind(this);
    // we need to compute the initial value
    this.state = {
      selection: JSON.parse(JSON.stringify(noSelection)),
      values: noSelectionValue
    };
  }

  reset() {
    this.setState(
      {
        selection: JSON.parse(JSON.stringify(noSelection)),
        values: noSelectionValue
      }
    );
  }

  updateFilter(selection: string, filterValue: Filter) {
    // hack?
    let v = this;
    return () => {
      console.log("clicked", selection, filterValue);
      v.setState(prevState => {
        let newState = JSON.parse(JSON.stringify(prevState));
        // update filter
        newState.selection[selection] = filterValue;
        // also update the result
        newState.values = countByFilter(data, newState.selection);
        return newState;
      });
    };
  }
  render() {
    let vis: JSX.Element[] = [];
    for (let i = 0; i < Nodes.length; i ++) {
      let n = Nodes[i];
      vis.push(<Chart
        dataset={[{x: "true", y: this.state.values[n].T}, {x: "false", y: this.state.values[n].F}]}
        baselineDataset={[{x: "true", y: noSelectionValue[n].T}, {x: "false", y: noSelectionValue[n].F}]}
        click={this.updateFilter}
        label={n}
      />);
    }
    return <div>
      {vis}
      <br/>
      <button onClick={this.reset}>reset</button>
    </div>;
  }
}


