import * as d3ScaleChromatic from "d3-scale-chromatic";

export interface Datum {
  x: string;
  y: number;
}

export enum Filter {
  true,
  false,
  none
};

export const Nodes = ["rain", "sprinkler", "grass"];

export interface Selection {
  [index: string]: Filter;
};

export interface Data {
  // rain: string;
  // sprinkler: string;
  // grass: string;
  [index: string]: string;
}

export interface Result {
  [index: string]: {
    T: number,
    F: number
  };
}

export function countByFilter(data: Data[], filter: Selection) {
  let r: Data[] = data;
  for (let i = 0; i < Nodes.length; i ++) {
    if (filter[Nodes[i]] === Filter.true) {
      r = r.filter((d) => d[Nodes[i]] === "T");
    } else if (filter[Nodes[i]] === Filter.false) {
      r = r.filter((d) => d[Nodes[i]] === "F");
    }
  }
  // now we count
  let counts: Result = {};
  for (let i = 0; i < Nodes.length; i ++) {
    counts[Nodes[i]] = {
      T: (r.filter(d => d[Nodes[i]] === "T").length),
      F: (r.filter(d => d[Nodes[i]] === "F").length)
    };
  }
  return counts;
}

// hack, oh well
interface CustomColorScale {
  [index: string]: string;
}

export const defaultColorScale: CustomColorScale = {
  0: d3ScaleChromatic.schemeSet1[1],
  1: d3ScaleChromatic.schemeSet1[3],
  2: d3ScaleChromatic.schemeSet1[4],
};


